.loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loading > div {
  position: relative;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -100px;
}


